httpService.$inject = ['$http']

function httpService($http) {
    // var http = this;
    // http.data = "";

    function httpRequest(request) {
        var res = "";
        $http(request).then(function(response) {
            console.log(response.data)
            res = response.data[0];
        });
        console.log("res")
        console.log(res)
    }

    return {
        httpRequest: httpRequest
    };
}
module.exports = httpService;


