auditController.$inject = ['$http', '$location'];

function auditController($http, $location) {
    var vm = this;
    $http({
        url: "/api/admin/manage/merchants/status/audit",
        method: 'GET'
    }).then(function(response) {
        console.log(response);
        vm.merchants = response.data;
    })

    vm.lookMerchantDetail = function(id) {
        console.log("look detail")
        $location.url('/manager/detailControl/' + id);
    }
}

module.exports = auditController;


