require('bootstrap/dist/css/bootstrap.min.css');
require('bootstrap/dist/css/bootstrap-theme.min.css');
module.exports = function($stateProvider) {
    $stateProvider
        .state({
            name: 'manager',
            url: '/manager',
            controller: require('../components/navbar/navbar.controller'),
            controllerAs: 'vm',
            template: require('../components/navbar/navbar.html')
        })
        .state({
            name: 'manager.audit',
            url: '/audit',
            controller: require('../controllers/auditControl.controller'),
            controllerAs: 'vm',
            template: require('../layouts/audit/audit.html')
        })
        .state({
            name: 'manager.bwControl',
            url: '/bwControl/:status',
            controller: require('../controllers/bwControl.controller'),
            controllerAs: 'vm',
            template: require('../layouts/bwControl/bwControl.html')
        })
        .state({
            name: 'manager.detailControl',
            url: '/detailControl/:id',
            controller: require('../controllers/detailControl.controller'),
            controllerAs: 'vm',
            template: require('../layouts/detailControl/detailControl.html')
        })
        .state({
            name: 'manager.dashboard',
            url: '/dashboard',
            controller: require('../controllers/dashboardControl.controller'),
            controllerAs: 'vm',
            template: require('../layouts/dashboardControl/dashboardControl.html')
        });
};


