var angular = require('angular');


var coreRoutes = require('./routes/core.routes');

var httpService = require('./services/http.service');


var navbarController = require('./components/navbar/navbar.controller');
// var navbarComponent = require('./components/navbar/navbar');
var auditController = require('./controllers/auditControl.controller');
var bwController = require('./controllers/bwControl.controller');
var detailController = require('./controllers/detailControl.controller');
const moduleName = 'core';

angular.module(moduleName, [])
    .config(coreRoutes)
    .factory('httpService', httpService)
    // .component('navbar', navbarComponent)
    .controller('navbarController', navbarController)
    .controller('auditController', auditController)
    .controller('bwController', auditController)
    .controller('detailController', auditController);

module.exports = moduleName;


